.qanda {
    font-size: .32rem;
    width: 100vw;
    height: 100vh;
    box-sizing: border-box;
    background-color: #f7f7f7;
    &-header{
        height: 1.5rem;
        background: plum;
        display: flex;
        justify-content: space-between;
        align-items: center;
        &-close {
            width: .8rem;
            height: .3rem;
            background: url(./assets/close.png) center center / contain no-repeat;
        }
    }
    &-wrapper {
        box-sizing: border-box;
        height: calc(100vh - 3.5rem);
        overflow-y: scroll;
    }
    &-footer {
        height: 2rem;
        display: flex;
        justify-content: center;
        align-items: center;
    }
    &-button {
        width: calc(100vw - .6rem);
        height: 1rem;
        border-radius: .5rem;
        background: linear-gradient(to right, #7E21E6, #4E79F4);
        display: flex;
        justify-content: center;
        align-items: center;
        font-size: .28rem;
        color: white;
        font-weight: bold;
    }
}

